.App {
  background-color:#000;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.body {
  color:#000;
}

.circle {
   border-radius:50%;
}

/* Two image container (50% width for two) */
.image-column {
	  float: left;
	  width: 50%;
	  padding: 20px;
      }

/* Clear floats after image containers */
.image-row::after {
	  content: "";
	  clear: both;
	  display: table;
	    }

/* Makes image columns stack on top of each other (not next to each other) */
@media screen and (max-width: 400px) {
.image-column {
	  float: left;
	  width: 100%;
	  padding: 30px 0px 30px 0px;  
      }
	    }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
   font-family: "Overpass",sans-serif;
   font-size: 2rem;
   font-weight: 500;
   line-height: 1.2;
   background-color:#000;
}

